// Pergunta.js
import React, { Component } from 'react';
import Slider from '../../utils/Slider';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import { TextField } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';

function TabContainer(props) {
    return (
        <Typography component="div" style={{ padding: 8 * 3 }}>
            {props.children}
        </Typography>
    );
}

const styles = theme => ({
    center: {
        justifyContent: 'center',
        marginTop: '2rem',
    },
    root: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.paper,
    }
})

const defValSlide = 50
const emocoes = ['Ternura', 'Divertimento', 'Alegria', 'Maravilhamento', 'Calma', 'Apreciação estética', 'Encantamento', 'Interesse', 'Desejo sexual', 'Romance', 'Desejo de comer', 'Nojo'];

// Main class
class Pergunta extends Component {

    constructor(props) {
        super(props);
        let state = {
            valence: -1,
            arousal: -1,
            step: 1,
            dislike: false,
            valueTab: 0
        }
        emocoes.forEach(el => {
            let emCode = 'emocao_' + el
            this.props.respostas(emCode, undefined)
            state[emCode] = 0
        })
        this.state = state;

        this.avancar = this.avancar.bind(this);
        this.handleTab = this.handleTab.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.enableSlide = this.enableSlide.bind(this);
        this.handleDislike = this.handleDislike.bind(this);
    }

    avancar = tabNum => (event, val) => {
        this.setState({ valueTab: tabNum });
    };

    handleTab = (event, value) => {
        this.setState({ valueTab: value });
    };

    handleChange = name => (event, val) => {
        const value = event.target.value || val
        this.setState({ [name]: value })
        this.props.respostas(name, value)
    }

    enableSlide = name => (event) => {
        let val = this.state[name]
        if( val === -1 ){
            val = defValSlide
            this.setState({ [name]: val })
            this.props.respostas(name, val)
        }
    }

    handleDislike(event, value) {
        this.setState({ dislike: value });
        this.props.respostas('dislike', value);
    }

    render() {
        if (!this.props.enabled) {
            return '';
        }

        const { valueTab } = this.state;
        const { valence, arousal, step, dislike } = this.state;
        const { classes, respondido } = this.props;
        const espaco = (<div style={{marginTop: 20}}></div>)
        return (
            <div className={classes.root}>

                <AppBar position="static">
                    <Tabs value={valueTab} onChange={this.handleTab}>
                        <Tab label="Parte 1" />
                        <Tab label="Parte 2" />
                    </Tabs>
                </AppBar>
                {/* TAB 1 */}
                {valueTab === 0 && <TabContainer>
                    <FormGroup row>
                        <FormControlLabel
                            control={
                                <Switch
                                    value="1"
                                    checked={dislike}
                                    onChange={this.handleDislike}
                                />
                            }
                            label="Não gostei"
                        />
                    </FormGroup>
                    {espaco}
                    <Slider
                        value={valence === -1 ? defValSlide : valence}
                        name='valence'
                        label='O quanto você gostou do vídeo?'
                        step={step}
                        onDragEnd={this.enableSlide("valence")}
                        onChange={this.handleChange("valence")}
                        disabled={dislike}
                        color={valence === -1 ? "secondary" : "primary"}
                        anchors={[
                            'Neutro / Não senti nada',
                            'Gostei um pouco',
                            'Gostei',
                            'Gostei bastante',
                            'Adorei'
                        ]}
                    />
                    {espaco}
                    <Slider
                        value={arousal === -1 ? defValSlide : arousal}
                        step={step}
                        label='O quanto o vídeo te deixou alerta/ligado?'
                        name="arousal"
                        disabled={dislike}
                        onDragEnd={this.enableSlide("arousal")}
                        onChange={this.handleChange("arousal")}
                        color={arousal === -1 ? "secondary" : "primary"}
                        anchors={[
                            'Nada alerta / Não senti nada',
                            'Pouco alerta',
                            'Alerta',
                            'Bastante alerta',
                            'Extremamente alerta'
                        ]}
                    />
                    <TextField
                        id="multiline-flexible"
                        label="Comentários"
                        multiline
                        fullWidth
                        rowsMax="2"
                        value={this.state.multiline}
                        onChange={this.handleChange('comentario')}
                        margin="normal"
                        style={{ marginTop: 0 }}
                    />

                    <Grid container className={classes.center}>
                        <Button variant="contained" color="primary"
                            disabled={dislike ? false : !respondido} onClick={this.avancar(1)}>
                            Avançar
                        </Button>
                    </Grid>
                </TabContainer>}

                {/* TAB 2 */}
                {valueTab === 1 && <TabContainer>
                    {emocoes.map((value, index) => {
                        const emocaoKey = 'emocao_' + value;
                        let curVal = this.state[emocaoKey] || 0
                        return <div key={"em-" + index}>
                            <Slider
                                value={curVal}
                                name={emocaoKey}
                                step={step}
                                pre={value}
                                pos={curVal}
                                onChange={this.handleChange(emocaoKey)}
                                anchor={[]}
                            />
                        </div>
                    })}

                    <Grid container className={classes.center}>
                        <Button variant="contained" color="primary"
                            disabled={dislike ? false : !respondido} onClick={this.props.submit}>
                            Salvar
                        </Button>
                    </Grid>
                </TabContainer>}


            </div>
        );
    }
}

export default withStyles(styles)(Pergunta);


