// TaskData.jsx
import Clip from "./Clip";

const apiUrl = 'https://apps.idor.org/api';

const storageKey = "clips-data";
export default class TaskData {

    static singleton = null;

    constructor(data = {}) {
        this.data = data;
        this.state = {};
    }

    addResposta(clipPath, state) {
        const clip = new Clip(clipPath)

        // Creating vector, if necessary
        if (this.data.answers === undefined) {
            this.data.answers = []
        }

        let answer = {
            clipId: clip.id,
            clipUrl: clip.url,
            valence: state.valence,
            arousal: state.arousal,
            comentario: state.comentario || "na",
            dislike: state.dislike,
            raw: state
        }

        this.data.answers.push(answer)
    }

    complete(){
        const answers = this.data.answers || [];
        const clips = this.data.clips || [];

        // When not started
        if (clips.length === 0) {
            return false;
        }

        return (answers.length >= clips.length)
    }

    nextAnswer() {
        try {
            if( !this.complete() ){
                return this.data.answers.length
            }
        } catch (e) {
            return 0
        }
    }

    upload() {
        return fetch(apiUrl + "/public/save", {
            method: 'PUT',
            body: JSON.stringify(this.data),
            headers: {
                "Content-type": "application/json; charset=UTF-8",
                "namespace": "clip",
            }
        })
    }

    // Check if is necessary save something
    started() {
        const clips = this.data.clips || [];
        return (clips.length > 0)
    }

    static checkServer() {
        return fetch(apiUrl + "/public/alive", {
            method: 'GET',
            body: JSON.stringify(this.data)
        }).then( (response) => {
            if (!response.ok) throw Error(response.statusText)
            return response;
        })
    }

    static getSingleton() {
        if (TaskData.singleton === null) {
            let savedData = JSON.parse(localStorage.getItem(storageKey));
            if( savedData === null ) {
                savedData = undefined
            }
            TaskData.singleton = new TaskData( savedData );
        }
        return TaskData.singleton;
    }

    static reload() {
        const data = JSON.parse(localStorage.getItem(storageKey));
        if (data) {
            TaskData.singleton = new TaskData(data);
        }
    }

    static save() {
        TaskData.singleton.__save();
    }

    static clear() {
        localStorage.setItem(storageKey, JSON.stringify({}));
        TaskData.singleton = new TaskData();
    }

    __save() {
        console.log("Salvando dados");
        localStorage.setItem(storageKey, JSON.stringify(this.data));
    }
}


