// Video.jsx
import React, { Component } from 'react';
import Clip from '../localEntities/Clip';
import TaskData from '../localEntities/TaskData';
import { withStyles } from '@material-ui/core/styles';

const styles = theme => ({
    video: {
        background: 'transparent'
    }
})

const taskData = TaskData.getSingleton()

// Main class
class Video extends Component {

    constructor(props) {
        super(props);

        const playedBefore = taskData.data.played || false;
        this.state = {
            played: playedBefore,
            visibility: 'visible',
            finished: playedBefore
        }

        this.play = this.play.bind(this);
        this.end = this.end.bind(this);
    }

    play() {
        this.setState({
            played: true,
            playing: true
        });
        let myVideo = document.getElementById("curVideo");
        myVideo.play();
    }

    end() {
        this.setState({
            finished: true,
            playing: false
        });
        taskData.data.played = true;
        TaskData.save()
        this.props.parentState('played', true)

        // Reloading video
        let video = document.getElementById("curVideo")
        video.load()
    }

    render() {
        const { classes } = this.props;
        const {playing} = this.state;

        let clip = new Clip(this.props.clip)
        return (
            <React.Fragment>
                <div className={classes.video}>
                    <video className={ playing && 'playing'}
                        id="curVideo"
                        width={'100%'}
                        poster={"imgs/transparent.png"}
                        volume={0}
                        onClick={this.play}
                        onEnded={this.end}
                    >
                        <source src={clip.url} type="video/mp4" />
                    </video>
                </div>
            </React.Fragment>
        );
    }
}

export default withStyles(styles)(Video);


