// Task.jsx
import React, { Component } from 'react';
import Tarefa from './components/Tarefa';
import Introducao from './components/Introducao';

import Clip from './localEntities/Clip';
import TaskData from './localEntities/TaskData';
import { Typography } from '@material-ui/core';
import {isMobile} from 'react-device-detect';

let taskData = TaskData.getSingleton();

function shuffle(array) {
    var currentIndex = array.length, temporaryValue, randomIndex;

    // While there remain elements to shuffle...
    while (0 !== currentIndex) {

        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;

        // And swap it with the current element.
        temporaryValue = array[currentIndex];
        array[currentIndex] = array[randomIndex];
        array[randomIndex] = temporaryValue;
    }

    return array;
}



export default class Task extends Component {

    constructor(props) {
        super(props);

        let state = 'cadastro';
        if (taskData.complete()) state='fim'
        else if (taskData.started()) state='tarefa'

        this.state = { state };
        this.clips = shuffle( Clip.listClips() );
        this.handleUpdate = this.handleUpdate.bind(this);
        this.handleError = this.handleError.bind(this);
        window.handleError = this.handleError;
    }

    componentDidMount() {
        
        // Checking connectivity
        TaskData.checkServer()
        .then( (res) => {
            // Try to save old data
            if (taskData.complete()) {
                taskData.upload()
                    .then(response => response.json())
                    .then(data => {
                        TaskData.clear()
                        taskData = TaskData.getSingleton();
                        this.setState({ state: 'fim' });
                    })
                    .catch(err => {
                        window.handleError(err, '[E002] Não foi possível salvar os dados da tarefa!')
                    });
                this.setState({state: 'wait'});
            }
        }).catch( err => window.handleError(err, '[E001] Servidor de dados não responde!'))
    }

    handleError = (err, msg) =>{
        window.disableClearBT()
        this.setState({
            state: 'error',
            err: msg
        });
    }

    handleUpdate(state, data) {
        // Updating subject data
        if (data !== undefined) {
            taskData.data = data;
        }

        // Updating screen, if necessary
        this.setState({ state: state });
    }

    render() {
        if (isMobile) {
            return <div>Aplicativo não funciona em dispositivos móveis. Utilize um computador.</div>
        }

        switch (this.state.state) {
            // Treino para a tarefa principal
            case 'cadastro':
                return (
                    <Introducao clips={this.clips} update={this.handleUpdate} />
                );

            // Tarefa principal
            case 'tarefa':
                return (
                    <React.Fragment>
                        <Tarefa
                            clips={taskData.data.clips}
                            update={this.handleUpdate}
                        />
                    </React.Fragment>
                );
            case 'error':
                return (
                    <React.Fragment>
                        <h2>Falha!</h2>
                        <p>Por favor, entre em contato com o responsável pela ferramenta e informe a mensagem de erro abaixo:</p>
                        <p style={{ color: 'red' }}>{this.state.err}</p>
                    </React.Fragment>
                );
            case 'fim':
                return (
                    <React.Fragment>
                        <Typography component="h1" variant="h4" align="center">
                            Tarefa concluída!
                        </Typography>
                        <p>Obrigado por colaborar com este estudo.</p>
                    </React.Fragment>
                );
            default:
                return (<React.Fragment>Aguarde...</React.Fragment>);
        }
    }
}


