// Cadastro.jsx
import React, { Component } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Grid, InputLabel, Select, FormControl, MenuItem, FormLabel } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';

const classes = theme => ({
    spaceTop: {
        marginTop: theme.spacing.unit * 2,
    }
});

const formDefaults = {
    sexo: 'feminino',
    email: '',
    orientacaoSexual: ''
};

class CadastroForm extends Component {
    constructor(props) {
        super(props);

        this.state = {
            open: true,
            formData: { ...formDefaults }
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleClose = this.handleClose.bind(this);
    }

    handleChange = event => {
        let formData = this.state.formData;
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        formData[name] = value;

        this.setState({
            'formData': formData
        })
    };

    handleClose = () => {
        if (parseInt(this.state.formData.idade, 10) >= 18) {
            this.setState({
                open: false
            });
            this.props.submit(this.state.formData)
        }
    };

    render() {
        const { classes } = this.props;
        const { sexo, orientacaoSexual } = this.state.formData;
        return (
            <React.Fragment>
                <Dialog
                    open={this.state.open}
                    aria-labelledby="form-dialog-title"
                >
                    <DialogTitle id="form-dialog-title">
                        Dados básicos
                    </DialogTitle>
                    <DialogContent>
                        <Grid container spacing={24}>
                            <Grid item xs={12}>
                                <TextField margin="dense"
                                    autoFocus
                                    name="idade"
                                    id="idade"
                                    label="Idade"
                                    type="number"
                                    onBlur={this.handleChange}
                                    required fullWidth
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField margin="dense"
                                    name="email"
                                    id="email"
                                    label="Email"
                                    type="text"
                                    onBlur={this.handleChange}
                                    required fullWidth
                                />
                            </Grid>
                            <Grid item xs={12} sm={12}>
                                <FormControl className={classes.formControl}>
                                    <InputLabel htmlFor="sexo">Sexo</InputLabel>
                                    <Select
                                        value={sexo}
                                        onChange={this.handleChange}
                                        name="sexo"
                                    >
                                        <MenuItem value="feminino">Feminino</MenuItem>
                                        <MenuItem value="masculino">Masculino</MenuItem>
                                    </Select>
                                </FormControl>
                            </Grid>

                            <Grid item xs={12} sm={12}>
                                <FormLabel component="legend"></FormLabel>
                                <FormControl className={classes.formControl}>
                                    <Select
                                        value={orientacaoSexual}
                                        onChange={this.handleChange}
                                        name="orientacaoSexual"
                                        displayEmpty
                                        className={classes.selectEmpty}
                                    >
                                        <MenuItem value="" disabled>
                                            Você se considera principalmente
                                        </MenuItem>
                                        <MenuItem value="heterossexual">Heterossexual</MenuItem>
                                        <MenuItem value="homossexual">Homossexual</MenuItem>
                                        <MenuItem value="bissexual">Bissexual</MenuItem>
                                        <MenuItem value="outro">Outro</MenuItem>
                                    </Select>
                                </FormControl>
                            </Grid>
                        </Grid>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={this.handleClose} color="primary">
                            Salvar
                        </Button>
                    </DialogActions>
                </Dialog>
            </React.Fragment>
        );
    }
}

export default withStyles(classes)(CadastroForm);


