// Header.js
import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { Typography, Toolbar, AppBar, IconButton } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete'
import TaskData from '../clips/localEntities/TaskData';
import ConfirmDialog from '../utils/ConfirmDialog';

const styles = theme => ({
    appBar: {
        position: 'relative',
    },
    button: {
        display: 'flex',
        justifyContent: 'flex-end',
        margin: theme.spacing.unit,
        color: 'white',
    },
    grow: {
        flexGrow: 1,
    }
});

class Header extends Component {
    
    constructor(props) {
        super(props);
        this.state = {
            open: false,
            disabled: false,
        }
        this.handleClose = this.handleClose.bind(this);
        this.clearClick = this.clearClick.bind(this);
        this.disableBt = this.disableBt.bind(this);
        window.disableClearBT = this.disableBt;
    }

    disableBt = () => {
        this.setState( {disabled: true})
    }

    handleClose = () => {
        this.setState({open: false})
    }

    clearClick = () => {
        this.setState({open: true})
    }

    clearSession = () => {
        TaskData.clear();
        window.location.reload();
    }

    render() {
        const { classes } = this.props;
        return (
            <React.Fragment>
                <AppBar position="static" className={classes.appBar}>
                    <Toolbar>
                        <Typography variant="h6" color="inherit" noWrap className={classes.grow}>
                            Clips
                        </Typography>
                        {!this.state.disabled && <IconButton className={classes.button} aria-label="Delete" color="secondary"
                            onClick={this.clearClick} hidden={this.disabled}>
                            <DeleteIcon />
                        </IconButton>}
                    </Toolbar>
                </AppBar>
                <ConfirmDialog 
                    title='Exclusão da tarefa'
                    text='Esta ação é irreversível. Confirma que deseja excluir seu progresso na tarefa?'
                    open={this.state.open}
                    handleClose={this.handleClose}
                    handleConfirm={this.clearSession}
                />
            </React.Fragment>
        )
    }
}

export default withStyles(styles)(Header);


