// Rap.jsx
import React, { Component } from 'react';
import Video from '../trial/Video';
import Pergunta from '../trial/Pergunta';
import TaskData from '../localEntities/TaskData';
import { Grid, LinearProgress } from '@material-ui/core';

const taskData = TaskData.getSingleton()

export default class Tarefa extends Component {

    constructor(props) {
        super(props);
        this.clips = props.clips;
        this.state = {
            clip: taskData.nextAnswer(),
            played: taskData.data.played || false,
            arousal: -1,
            valence: -1,
            comentario: '',
            dislike: false
        };

        this.btInit = {
            text: 'Salvar',
            style: 'success',
        };

        this.handleRespostas = this.handleRespostas.bind(this);
        this.handleNext = this.handleNext.bind(this);
        this.nextState = 'fim';
    }

    respondido() {
        const { arousal, valence } = this.state;
        return (arousal >= 0 && valence >= 0);
    }

    handleRespostas(key, value) {
        this.setState((state) => { return { [key]: value } })
    }

    handleNext() {
        const state = this.state
        taskData.addResposta(this.clips[state.clip], state)
        taskData.data.played = undefined;
        TaskData.save()

        // Saving data via API when using last clip
        if (taskData.complete()) {
            window.disableClearBT()
            taskData.upload()
                .then(response => response.json())
                .then(data => {
                    TaskData.clear()
                    this.props.update('fim')
                })
                .catch(err => {
                    window.handleError(err, '[E003] Não foi possível salvar os dados da tarefa! Entre em contato com o responsável pela ferramenta.')
                });
            return null;
        }

        this.setState({
            clip: state.clip + 1,
            played: false,
            arousal: -1,
            valence: -1,
            comentario: '',
            dislike: false
        });
    }

    render() {
        const { clip, played } = this.state;
        const progress = ((clip+1)*100 / this.clips.length);
        return (
            <div className="container-fluid">
                <LinearProgress variant="determinate" value={progress} style={{margin:'-1.5rem -1.4rem 1.5rem'}}/>
                <main id="content" role="main" className="col-sm-12 ml-sm-auto col-md-12 pt-2">
                    <section className="row justify-content-md-center">
                        <Grid container spacing={24}>
                            <Grid item xs={6}>
                                <Pergunta
                                    key={'pergunta-' + clip}
                                    enabled={played}
                                    respondido={this.respondido()}
                                    respostas={this.handleRespostas}
                                    submit={this.handleNext}
                                />
                            </Grid>

                            <Grid item xs={ played ? 6 : 12}>
                                <Video
                                    key={'clip-' + clip}
                                clip={this.clips[clip]}
                                visible={true}
                                parentState={this.handleRespostas}
                                />
                            </Grid>
                        </Grid>
                    </section>
                </main>
            </div >
        );
    }
}


