import React from 'react';
import Typography from '@material-ui/core/Typography';
import SliderMatUI from '@material-ui/lab/Slider';
import Grid from '@material-ui/core/Grid';
import './Slider.module.css';
import { withStyles } from '@material-ui/core/styles';

const styles = theme => ({
    root: {
        width: 'auto',
        fontSize: '0.7rem'
    },
    niveis: {
        padding: '0 10px',
        display: 'flex',
        flexDirection: 'row',
        flexWrap: 'nowrap',
        justifyContent: 'space-between',
        alignItems: 'flex-end'
    },
    ancora: {
        width: '100'
    },
    slider: {
        padding: '22px 0px',
    },
    toRight: {
        textAlign: "right", paddingRight: '10px'
    },
    toLeft: {
        textAlign: "left", paddingLeft: '10px'
    },
    label: {
        marginBottom: 10
    },
    secondary: {
        backgroundColor: "#f50057"
    },
})

class Slider extends React.Component {
    render() {
        const { name, label, value, step, anchors, min, max, onChange, classes, pre, pos } = this.props;
        let gridSize = 12
        if (pre) gridSize = gridSize - 2
        if (pos !== undefined) gridSize--

        let extraClasses = {};
        if (this.props.color === 'secondary'){
            extraClasses['thumbIconWrapper'] = classes.secondary;
            extraClasses['thumb'] = classes.secondary;
            extraClasses['track'] = classes.secondary;
        }

        return (
            <div id={name}>
                {(label !== undefined) && <Typography id="label" name={name} className={classes.label}>{label}</Typography>}

                <Grid container alignItems={"center"} className={classes.root}>
                    {/* Levels */}
                    {anchors && <Grid container item xs={12} alignItems={"center"} className={`${classes.niveis} slider-label`}>
                        {
                            anchors.map(function (text, idx) {
                                return (
                                    <div className='item' key={idx}>
                                        <div className='ancora'>{text}</div>
                                        <div className='barra'></div>
                                    </div>
                                )
                            })
                        }
                    </Grid>
                    }

                    {/* Slider */}
                    <Grid container>
                        {pre && <Grid item xs={2}>{pre}</Grid>}
                        <Grid item xs={gridSize} style={{ padding: '0 4.13em' }}>
                            <SliderMatUI
                                classes={{ container: classes.slider, ...extraClasses }}
                                value={value}
                                step={step}
                                aria-labelledby="label"
                                onChange={onChange}
                                onDragEnd={this.props.onDragEnd}
                                onDragStart={this.props.onDragStart}
                                min={min}
                                max={max}
                                disabled={this.props.disabled}
                            //{...this.props}
                            />
                        </Grid>
                        {pos && <Grid item xs={1}>{pos}</Grid>}
                    </Grid>
                </Grid>
            </div>
        );
    }
}

export default withStyles(styles)(Slider);


