// Cadastro.jsx
import React, { Component } from 'react';
import classNames from 'classnames';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import { Typography } from '@material-ui/core';
import CadastroForm from './CadastroForm';

const styles = theme => ({
    list: {
        "& li:not(:last-child)": {
            marginBottom: '.8em'
        }
    },
    nobullets: {
        listStyle: 'none'
    },
    center: { 
        textAlign: 'center', 
        marginTop: 50 
    }
})

class Introducao extends Component {
    state = {
        open: true,
        formData: {}
    };

    handleIniciar = (event) => {
        const nextState = 'tarefa';
        let formData = this.state.formData;
        formData['clips'] = this.props.clips;

        this.props.update(nextState, formData);
    }

    render() {
        const {classes} = this.props;
        return (
            <React.Fragment>
                <Typography component="h1" variant="h4" align="center">
                    Introdução
                </Typography>
                <p>Olá, nas próximas páginas você verá diferentes vídeos bem curtos (6s). Esses vídeos serão relacionados a diferentes temas, como ternura (bebês e filhotes), deslumbramento  com a natureza, desejo sexual e gula/vontade de comer.</p>
                <p>Após ver cada vídeo, você deverá responder um questionário dividido em duas partes:</p>
                <h4>PARTE 1</h4>
                <ul className={classNames(classes.list, classes.nobullets)}>
                    <li>1) o quanto você gostou do vídeo (achou positivo ou negativo) e;</li>
                    <li>2) o quanto o vídeo te deixou <i>alerta/ligado/desperto/pilhado;</i></li>
                    <li>3) se você não gostou do vídeo, apenas clique na primeira opção "não gostei" e ignore os slides;</li>
                    <li>4) por favor escreva no campo "comentário" se você perceber algum problema com os vídeos, por que não gostou de algum vídeo, ou qualquer informação que achar relevante.</li>
                </ul>

                <h4>PARTE 2</h4>
                <ul className={classes.list}>
                    <li>Marque o quanto você sentiu cada uma das emoções ao ver o vídeo.</li>
                </ul>


                <h4>Observações</h4>
                <ul className={classes.list}>
                    <li>Você pode rever os vídeos quantas vezes quiser.</li>
                    <li>Você pode utilizar todo o espaço dos slides para marcar sua resposta</li>
                </ul>

                <div className={classes.center}>
                    <Button variant="contained" color="primary" onClick={this.handleIniciar}>Iniciar</Button>
                </div>
                <CadastroForm submit={(formData) => this.setState({formData})}/>
            </React.Fragment>
        );
    }
}

export default withStyles(styles)(Introducao);


